body {
    font-family: 'Roboto', sans-serif;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    margin: 0;
}

.header {
    background-color: #f8f9fa;
    padding: 10px 20px;
}

#root {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}

.footer {
    margin-top: auto;
    background: #333;
    color: #fff;
    padding: 10px;
    text-align: center;
}

.card {
    border: none;
    text-align: center;
    transition: transform 0.3s ease;
}

.card:hover {
    transform: scale(1.05);
}

@media (max-width: 768px) {
    .navbar {
        flex-direction: column;
    }
}

.login-page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: #f5f5f5;
}

.login-box {
    padding: 20px;
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: 400px; /* Increase width */
    max-width: 90%; /* Responsive */
    text-align: center;
}

.login-box h2 {
    margin-bottom: 20px;
}

.login-box input {
    display: block;
    width: 70%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.login-box button {
    width: 100%;
    padding: 10px;
    background: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.register-page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: #eef2f3;
}

.register-box {
    padding: 20px;
    background: #ffffff;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    text-align: center;
    max-width: 400px;
    width: 100%;
}

.register-box h2 {
    margin-bottom: 20px;
    font-family: 'Roboto', sans-serif;
}

.register-box input {
    display: block;
    width: 70%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.register-box button {
    width: 100%;
    padding: 10px;
    background: #28a745;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
}

.register-box button:hover {
    background: #218838;
}

.register-box p {
    margin-top: 15px;
    font-size: 14px;
    color: #666;
}

.register-box a {
    color: #007bff;
    text-decoration: none;
}

.register-box a:hover {
    text-decoration: underline;
}

.error-message {
    color: #d9534f;
    margin-bottom: 10px;
    font-weight: bold;
}

/* Loading Bar */
.loading-bar {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: #eef2f3;
}

.bar {
    width: 50%;
    height: 5px;
    background: linear-gradient(90deg, #007bff, #28a745);
    background-size: 200% 100%;
    animation: loading 2s linear infinite;
}

@keyframes loading {
    from {
        background-position: 200% 0;
    }
    to {
        background-position: -200% 0;
    }
}


.loading-animation {
    text-align: center;
}

.spinner {
    border: 4px solid #f3f3f3;
    border-top: 4px solid #3498db;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: spin 1s linear infinite;
    margin: 0 auto 1rem auto;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}



/* Fade-in Animation */
.fade-in {
    opacity: 0;
    animation: fadeIn 1s ease-in forwards;
}

@keyframes fadeIn {
    to {
        opacity: 1;
    }
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
